import React from 'react';
import { Controller, Block, RenderNodeProps } from '@ali/4ever-cangjie';
import MobileCodeEditor from '../components/MobileCodeEditor';
import { CodeLocale } from '../types';
import { CodeWrapper } from '../../utils/constants/themes';

interface IProps {
  controller: Controller;
  node: RenderNodeProps<Block>['node'];
  attributes: RenderNodeProps<Block>['attributes'];
  locale: CodeLocale;
  isSelected: boolean;
}

export const MobileCode: React.FC<IProps> = ({
  locale,
  attributes,
  node,
  controller,
}) => {
  return (
    <CodeWrapper 
      {...attributes} 
      data-testid="code-editor" 
      data-type="code">
      <MobileCodeEditor
        controller={controller}
        node={node}
        locale={locale}
      />
    </CodeWrapper>
  );
};
